<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
    <div>
      <svg-icon />

      <pswp />
      <reward />
      <payfor />
      <check-in />
      <post-menu />
      <actionsheet />
      <comment-input />

      <apply-top />
      
      <info-tips />
      <choose-tags />

    </div>
  </div>
</template>
<script>
// 
import pswp from "@/components/pswp.vue";
// 
import PostMenu from "@/page/PostMenu.vue";
// 
import payfor from "@/components/payfor.vue";
// 
import actionsheet from "@/components/actionsheet.vue";
// 
import commentInput from "@/components/commentInput.vue";
// icons
import SvgIcon from "@/components/common/svgIcon.vue";
// 
import CheckIn from "@/page/checkin/CheckIn.vue";
// 
import Reward from "@/components/reward.vue";
// 
import chooseTags from "@/page/chooseTags.vue";
//  
import infoTips from "@/components/infoTips.vue";
// 
import applyTop from "@/components/applyForTop.vue";

export default {
  name: "app",
  components: {
    pswp,
    payfor,
    actionsheet,
    commentInput,
    infoTips,
    applyTop,

    SvgIcon,

    Reward,
    CheckIn,
    PostMenu,
    chooseTags
  }
};
</script>
<style lang="less">
.router-fade-enter-active,
.router-fade-leave-active {
  transition: opacity 0.3s;
}

.router-fade-enter,
.router-fade-leave-active {
  opacity: 0;
}
</style>



