/*  */
const find = () => import(/* webpackChunkName: 'find' */ "@/page/find/find");
const findContent = () =>
  import(/* webpackChunkName: 'find' */ "@/page/find/children/findContent");
const findNearby = () =>
  import(/* webpackChunkName: 'find' */ "@/page/find/children/findNearby");

export default [
  {
    path: "/find",
    component: find,
    meta: {
      title: "",
      requiresAuth: true
    },
    redirect: "/find/pop",
    children: [
      {
        path: "new",
        component: findContent,
        meta: {
          type: "new",
          title: "",
          keepAlive: true
        }
      },
      {
        path: "pop",
        component: findContent,
        meta: {
          type: "pop",
          title: "",
          keepAlive: true
        }
      },
      {
        path: "rec",
        component: findContent,
        meta: {
          type: "rec",
          title: "",
          keepAlive: true
        }
      },
      {
        path: "nearby",
        component: findNearby,
        meta: {
          title: "",
          keepAlive: true
        }
      }
    ]
  }
];



